.contenedor {
    width: 75%;
    max-width: 100rem;
    margin: 0 auto;

    .tabla{
        background-color: $azul;
        margin: 4rem 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 1rem;
        border: 2px solid $azulFuerte;
        box-shadow: 1rem 1rem 1rem $gris;

        @include tablet{
            display: grid;
            grid-template-columns: 1fr 2fr;
            gap: 2rem;
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            align-items: center;
        }

        .img-mascota{
            max-width: 30rem;
            margin: 2rem 1rem;
            border-radius: 50%;
            box-shadow: 1rem 1rem 1rem $gris;

            img{
                border-radius: 50%;
            }
        }

        .info-mascota{
            background-color: $blanco;
            margin: 3rem 2rem;
            border-radius: 1rem;
            border: 3px solid $azulFuerte;

            h1{
                font-size: 4rem;
                color: $naranja;
                font-weight: $gruesa;
            }

            @include tablet{
                h1{
                    margin: 0;
                }
            }

            .caracteristicas{
                padding: 0 2rem;

                span{
                    color: $azulFuerte;
                    font-weight: $gruesa;
                    font-size: 2rem;
                }
            }
        }
    }
}