@import url('https://fonts.googleapis.com/css2?family=Marck+Script&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Amaranth:wght@400;700&display=swap');

// Fuentes
$fuentePrincipal: 'Amaranth', sans-serif;
$fuenteSecundaria: 'Marck Script', cursive;

// Tamaño de Media Queries
$telefono: 480px;
$tablet: 768px;
$desktop: 1024px;

// Colores
$negro: #000000;
$blanco: #FFFFFF;
$azul: #c0d6df;
$azulFuerte: #4f6d7a;
$beige: #e8dab2;
$naranja: #dd6e42;
$gris: rgb(100, 100, 100);

// Separacion
$separacion: 5rem;

// Tamaños de fuente
$regular : 400;
$gruesa: 700;