html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuentePrincipal;
    font-size: 1.4rem;
    line-height: 1.5;
    background-image: url(/build/img/huellitas.jpg);
    background-size: cover;
}

p {
    color: $negro;
    font-size: 1.8rem;
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    font-family: $fuenteSecundaria;
    text-align: center;
    
}